class RestModel
{
    constructor(controller,success,error=null)
    {

        this.controller=controller;//
        this.success=success;//
        this.error=error;//
        this.model=null;//
        this.id=null;//id,getdelete
        this.paramModel=null;//
        this.paramModelName="paramModel";//,,
        this.pageModel=null;//
        this.async=true;//
        this.timeout=25000;//
        this.corsUrl="/";//


    }
}
module .exports=RestModel;



