   var ajax= require("./ajax.js");
//RSST
var rest={
    validate:function(settings,type) {
        if (!settings || !settings instanceof  Object) {
            throw new Error("ajax,,");
            return  false;
        }

        if(!settings.controller) {//
            throw new Error("ajaxcontroller[]");
            return  false;
        }

        if(!settings.corsUrl) {//,
            settings.corsUrl = "/";
        }
        else if(typeof settings.corsUrl !=="string")
        {
            throw  new Error("corsUrl ");
            return  false;
        }
        if(!settings.success) {
            throw new Error("ajaxsuccess[]");
            return  false;
        }
        else if(typeof settings.success !=="function")
        {
            throw new Error("ajaxsuccess[]");
            return  false;
        }

        if(settings.error&&typeof settings.error !=="function") {
            throw new Error("ajaxerror[]");
            return  false;
        }

        if(!type) {
            switch (type)
            {
                case "get"://
                    if(settings.id==null||settings.id==undefined)
                    {
                        throw  new Error("id");
                        return  false;
                    }
                    else if(typeof settings.id !=="number")
                    {
                        throw  new Error("id");
                        return  false;
                    }
                    break;
                case "add"://
                    if(settings.model ==null||settings.model==undefined)
                    {
                        throw new Error("");
                        return  false;
                    }
                    break;
            }
        }
        return  true;
    },
    //
    getModel:function(settings) {
        /// <summary>
        /// 
        /// </summary>
        /// <param name="settings" type="object">settings</param>
        if(!settings.corsUrl) {//,
            settings.corsUrl = "/";
        }
        if(this.validate(settings)) {
            ajax({
                type: "GET",
                url: settings.corsUrl + settings.controller + "/GetModel",
                async:settings.async,
                dataType: "json",
                timeout: settings.timeout?settings.timeout:25000,
                success: settings.success,
                error: settings.error,
            });
        }
    },
    //
    get:function() {
        /// <summary>
        /// 
        /// </summary>
        /// <param name="settings" type="object">settings</param>
        if(!settings.corsUrl) {//,
            settings.corsUrl = "/";
        }
       if(this.validate(settings,"get"))
       {
           ajax({
               type: "GET",
               url: settings.corsUrl + settings.controller+ "/Get?id="+id,
               async:settings.async,
               dataType: "json",
               timeout: settings.timeout?settings.timeout:25000,
               success: settings.success,
               error: settings.error,
           });
       }


    },
    //
    add:function(settings) {
        /// <summary>
        /// 
        /// </summary>
        /// <param name="settings" type="object">settings</param>
        if(!settings.corsUrl) {//,
            settings.corsUrl = "/";
        }
        var data={};
       if( this.validate(settings,"add")) {
           if(settings.model instanceof  Array)
           {//
               data={model:settings.model};
           }
           else
           {
               data=settings.model;
           }

           ajax({
               type: "POST",
               url: settings.corsUrl + settings.controller+ "/Add",
               async:settings.async,
               dataType: "json",
               timeout: settings.timeout?settings.timeout:25000,
               data:data,
               success: settings.success,
               error: settings.error,
           });
       }


    },
    //
    update:function(settings) {
        /// <summary>
        /// 
        /// </summary>
        /// <param name="settings" type="object">settings</param>
        if(!settings.corsUrl) {//,
            settings.corsUrl = "/";
        }
        var data={};//
        if( this.validate(settings,"add")) {
            if(settings.model instanceof  Array)
            {//
                data={model:settings.model};
            }
            else
            {
                data=settings.model;
            }

            ajax({
                type: "POST",
                url: settings.corsUrl + settings.controller+ "/Update",
                async:settings.async,
                dataType: "json",
                timeout: settings.timeout?settings.timeout:25000,
                data:data,
                success: settings.success,
                error: settings.error,
            });
        }

    },
    //
    delete:function(settings) {
        /// <summary>
        /// 
        /// </summary>
        /// <param name="settings" type="object">settings</param>
        if(!settings.corsUrl) {//,
            settings.corsUrl = "/";
        }
        var type="POST";//
       if(this.validate(settings)) {


           var url = settings.corsUrl + settings.controller + "/Delete";
           var data = {};//
           if (settings.paramModel instanceof Array) {
               if (settings.paramModelName) {//,
                   if (typeof  settings.paramModelName === "string") {
                       data[settings.paramModelName] = settings.paramModel;
                   }
                   else {
                       throw  new Error("paramModelName[]");
                       return false;
                   }
               }
               else {
                   data.paramModel = settings.paramModel;//
               }
           }
           else if (typeof  (paramModel * 1) === "number") {//

               type = "GET";
               url = url + "?id=" + paramModel;
               data = null;
           }
           else {
               throw new Error("paramModel,id");
               return false;
           }

           ajax({
               type: type,
               url: url,
               async:settings.async,
               dataType: "json",
               timeout: settings.timeout ? settings.timeout : 25000,
               data: data,
               success: settings.success,
               error: settings.error
           });
       }
    },
    //
    query:function( settings) {
        /// <summary>
        /// 
        /// </summary>
        /// <param name="settings" type="array">settings</param>
        if(!settings.corsUrl) {//,
            settings.corsUrl = "/";
        }
        if(this.validate(settings)) {
            var data={};//

            if (!settings.paramModel||(settings.paramModel && settings.paramModel instanceof Array)) {
                if (settings.paramModelName) {//,
                    if (typeof  settings.paramModelName === "string") {
                        data[settings.paramModelName] = settings.paramModel;
                    }
                    else {
                        throw  new Error("paramModelName[]");
                        return false;
                    }
                }
                else {
                    data.paramModel = settings.paramModel;//
                }
            }
            else {
                throw  new Error("paramModel[],");
                return false;
            }



            ajax({
                type: "POST",
                url: "/" + settings.controller + "/Query",
                async:settings.async,
                dataType: "json",
                timeout: settings.timeout?settings.timeout:25000,
                data:data,
                success: settings.success,
                error:settings. error
            });
        }

    },
    //
    page:function(settings) {
        /// <summary>
        /// 
        /// </summary>
        /// <param name="settings" type="object">settings</param>
        if(!settings.corsUrl) {//,
            settings.corsUrl = "/";
        }
        if(this.validate(settings)) {
            if (settings.pageModel && settings.pageModel instanceof Object) {
                //
                if (!settings.pageModel.paramModel||(settings.pageModel.paramModel && settings.pageModel.paramModel instanceof Array)) {
                  //
                }
                else {
                    //
                    throw  new Error("pageModelparamModel[],");
                    return false;
                }

            }
            else {
                throw  new Error("pageModel[],");
                return false;
            }
            ajax({
                type: "POST",
                url: "/" + settings.controller + "/Page",
                async: settings.async,
                dataType: "json",
                timeout: settings.timeout ? settings.timeout : 25000,
                data: settings.pageModel,
                success: settings.success,
                error: settings.error
            })
        }

    },
};

module .exports=rest;


