var paramFormat = require("./paramFormat.js");
var httpCode = require("./httpCode.js");
//ajax
var ajax = function (settings) {
  var xhrRequest = createXHR();
  if (!validate()) {//
    return;
  }
  
  if (xhrParamsSet()) {//
    
    //
    if (settings.data) {
      if (settings.type.toLowerCase() == "get") {
        xhrRequest.send();
      }
      else {//post
        xhrRequest.send(settings.data);
      }
    }
    else {
      xhrRequest.send();
    }
  }
  else {
    
  }
  
  
  /**
   * xhr
   * @returns {*}
   */
  function createXHR() {
    var xhr;
    if (window.XMLHttpRequest) {
      xhr = new XMLHttpRequest();
    } else {
      xhr = new ActiveXObject('Microsoft.XMLHTTP');
    }
    return xhr;
    
  }
  
  /**
   * 
   * @returns {boolean}
   */
  function validate() {
    if (!xhrRequest) {
      throw new Error("ajax");
      return false;
    }
    if (!settings || !settings instanceof Object) {
      throw new Error("ajax,,");
      return false;
    }
    if (settings.data instanceof Array) {
      throw new Error("ajaxdata,,,FormData,");
      return false;
    }
    if (!settings.dataType) {//,json
      settings.dataType = "json";
    }
    if (!settings.type) {//
      settings.type = "GET";
    }
    if (settings.async!==false) {
      settings.async = true;//
    }
    if (settings.url == null || settings.url == undefined || settings.url === "") {
      throw new Error("");
      return false;
    }
    if (!settings.success) {
      throw new Error("ajaxsuccess[]");
      return false;
    }
    else if (typeof settings.success !== "function") {
      throw new Error("ajaxsuccess[]");
      return false;
    }
    
    if (settings.error && typeof settings.error !== "function") {
      throw new Error("ajaxerror[]");
      return false;
    }
    if (settings.progress && typeof settings.progress !== "function") {
      throw new Error("ajaxprogress[]");
      return false;
    }
    if (settings.data && settings.data.constructor === FormData) {//FormDatajquery ajaxcontentType=false,processData=false,Content-Type
      settings.contentType == false;
    }
    else if (settings.contentType == false) {//false
      
    }
    else if (settings.contentType == null || settings.contentType == undefined || settings.contentType == "") {//,
      //false
      settings.contentType = "application/x-www-form-urlencoded";//
    }
    
    //FormData
    settings.data = paramFormat(settings.data);
    
    if (settings.type.toLowerCase() == "get") {
      if (settings.data && settings.url.indexOf("?") <= -1) {
        settings.url += "?";
      }
      if (settings.data && settings.url.indexOf("?") > -1 && settings.url.indexOf("?") == settings.url.length - 1) {
        settings.url += settings.data;
      }
      else if (settings.data && settings.url.indexOf("?") > -1 && settings.url.indexOf("?") < settings.url.length - 1) {
        settings.url += "&" + settings.data;
      }
    }
    
    return true;
  }
  
  /**
   * xhr
   */
  function xhrParamsSet() {
    try {
      xhrRequest.open(settings.type, settings.url, settings.async);
    }
    catch (e) {//
      errorHandler(xhrRequest, 803, "[IE,360]:" + e.message);
    }
    
    //
    if (settings.contentType == false) {//false,Content-Type
    }
    else {
      xhrRequest.setRequestHeader("Content-Type", settings.contentType);//,
    }
    //
    try {
      xhrRequest.responseType = settings.dataType;//
    }
    catch (e) {
      console.log("[IE360]responseType");
    }
    try {
      xhrRequest.withCredentials = settings.cors ? true : false;//(cross-site)(Access-Control)(cookieheader)  false
    }
    catch (e) {
      console.log("[IE360]withCredentials");
    }
    if (!settings.timeout) { //
      xhrRequest.timeout = settings.timeout;//
    }
    
    try {
      
      xhrRequest.addEventListener("load", load, false);///
      xhrRequest.addEventListener("loadend", loadEnd, false);//
      xhrRequest.addEventListener("timeout", timeout, false);//
      xhrRequest.addEventListener("error", error, false);//
      if (typeof  settings.progress === "function") {//
        xhrRequest.upload.addEventListener("progress", progress, false);//
      }
      else {
        
      }
    }
    catch (e) {//xhr2.0
      console.log("xhr2.01.0");
      xhrRequest.onreadystatechange = function () {
        if (xhrRequest.readyState == 4) {
          var e = {};
          e.target = xhrRequest;
          load(e);//
        }
      }
    }
    return true;
    
  }
  
  /**
   * 
   * @param event
   */
  function progress(event) {
    if (event.lengthComputable) {
      var percentComplete = Math.round(event.loaded * 100 / event.total);
      if (typeof  settings.progress === "function") {
        settings.progress(percentComplete);//
      }
    }
  }
  
  /**
   * 
   * @param event
   */
  function load(event) {
    var xhr = (event.target);
    if (xhr.readyState == 4 && ((xhr.status >= 200 && xhr.status < 300) || xhr.status == 304)) {//
      if (settings.dataType == "json") {
        //json
        var result = xhr.response?xhr.response:xhr.responseText;//1.0
        if (result) {
          if (typeof result == "string") {//IE8.360 JSON
            result = JSON.parse(result);
          }
          if (result.success != null && result.success != undefined) {//
            if (result.success) {
              if (settings.success && typeof settings.success === "function") {
                settings.success(result);//
              }
              else {
                throw  new Error("-success");
              }
            }
            else {
              if (result.message) {//
                errorHandler(result, result.errCode, result.message);
              }
              else {
                errorHandler(result, 801, "");
                
              }
            }
          }
          else {//
            if (settings.success && typeof settings.success === "function") {
              settings.success(result);//
            }
            
            else {
              throw  new Error("-success");
            }
            
          }
        }
        else {
          errorHandler(xhr, 802, "");
        }
      }
      else if (settings.dataType == "blob" || settings.dataType == "arrayBuffer") {//
        settings.success(xhr.response);
      }
      else {//
        try {
          settings.success(xhr.responseText);
        }
        catch (e) {//responseText,if,
          settings.success(xhr.response);
        }
        
      }
    }
    else {//4xxNetwork error,error
      
      errorHandler(xhr, xhr.status, xhr.statusText);
    }
    
    
  }
  
  /**
   * 
   * @param event
   */
  //
  function loadEnd(event) {
    var xhr = (event.target);
    if (typeof settings.complete === "function") {//,
      if (xhr.readyState == 4 && ((xhr.status >= 200 && xhr.status < 300) || xhr.status == 304)) {//
        //304GET,
        settings.complete(xhr, "success");
      }
      else if (xhr.readyState == 4 && xhr.status == 0) {//TODO 
        
      }
      else {//
        settings.complete(xhr, "error");
      }
    }
  }
  
  /**
   * 
   * @param event
   */
  function timeout(event) {
    var xhr = (event.target);
    errorHandler(xhr, 802, "");
  }
  
  /**
   * 
   * @param event
   */
  function error(event) {
    var xhr = (event.target);
    errorHandler(xhr, xhr.status, xhr.statusText);
  }
  
  /**
   * 
   * @param xhr
   * @param errCode
   * @param message
   */
  function errorHandler(xhr, errCode, message) {
    
    if (errCode >= 300 && errCode < 600) {
      
      console.log(errCode, httpCode[errCode.toString()]);//http
      if (typeof settings.error === "function") {//,
        settings.error(xhr, errCode, httpCode[errCode.toString()]);
      }
    }
    else {
      console.log(errCode, message);
      if (typeof settings.error === "function") {//,
        settings.error(xhr, errCode, message);
      }
    }
    
    
  }
}

module.exports = ajax;





